import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

import AddIcon from '@mui/icons-material/Add';
import CloseIcon from '@mui/icons-material/Close';
import {
	Container,
	Stack,
	Typography,
	Button,
	Box,
	TextField,
	IconButton,
	Modal,
	Grid,
	Select,
	MenuItem,
} from '@mui/material';
import { SelectChangeEvent } from '@mui/material/Select';

import api from '../../api/api';
import ShopCategoryIcon from '../../components/ShopCategoryIcon';

const modalStyle = {
	position: 'absolute',
	top: '50%',
	left: '50%',
	transform: 'translate(-50%, -50%)',
	width: '60%',
	bgcolor: 'background.paper',
	borderRadius: 4,
	boxShadow: 24,
	p: 4,
};

//  
const shops = [
	{
		shopType: 1,
		shopName: '',
	},
	{
		shopType: 2,
		shopName: '',
	},
	{
		shopType: 3,
		shopName: '',
	},
	{
		shopType: 4,
		shopName: '',
	},
	{
		shopType: 5,
		shopName: '',
	},
	{
		shopType: 6,
		shopName: '',
	},
	{
		shopType: 7,
		shopName: '',
	},
];

function ShopAdd() {
	// modal 
	const [open, setOpen] = useState(false);
	const handleOpen = () => setOpen(true);
	const handleClose = () => setOpen(false);

	//  
	const [shopType, setShopType] = useState(0);
	const selectShopType = (e: React.MouseEvent<HTMLButtonElement>) => {
		setShopType(Number(e.currentTarget.dataset.shopType));
		handleClose();
	};

	// shop 
	const [shopName, setShopName] = useState('');
	const changeShopName = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
		setShopName(e.target.value);
	};

	const navigate = useNavigate();

	//   
	const validate = (type: number, name: string) => {
		if (!type || !name) {
			alert(' !');
			return false;
		}

		return true;
	};

	//  
	const clickCancleBtn = () => {
		navigate('/shop');
	};

	// shop 
	const [shopCountryCode, setShopCountryCode] = useState('');
	const submitShop = async () => {
		if (confirm('?')) {
			const data = {
				shop_type: shopType,
				shop_name: shopName,
				shop_nation_code: shopCountryCode,
			};

			if (!validate(shopType, shopName)) return; //  

			try {
				const res = await api.post(`/shop`, data);
				if (res.status === 200) {
					alert('');
					navigate('/shop');
				}
			} catch (error: any) {
				if (error.response.status == 401) {
					navigate('/shop');
				} else {
					window.alert('shop  ');
				}
			}
		}
	};

	//  or  
	const { state } = useLocation();

	//     
	useEffect(() => {
		if (state.isModify) {
			setShopType(state.shopType);
			setShopName(state.shopName);
			setShopCountryCode(state.shopNationCode);
		}
	}, [state]);

	// shop 
	const modifyShop = async () => {
		if (confirm('?')) {
			const data = {
				shop_id: state.shopId,
				shop_type: shopType,
				shop_name: shopName,
			};

			if (!validate(shopType, shopName)) return; //  

			try {
				const res = await api.patch(`/shop`, data);
				if (res.status === 200) {
					alert('');
					navigate('/shop');
				}
			} catch (error: any) {
				if (error.response.status == 401) {
					window.location.replace('/shop');
				} else {
					window.alert('shop  ');
				}
			}
		}
	};

	//  
	interface Country {
		country_code: string;
		country_name: string;
	}
	const [countries, setCountries] = useState<Country[]>([]);

	const getCountry = async () => {
		const res = await api.get(`/country`);
		setCountries(res.data);
	};
	useEffect(() => {
		if (countries.length === 0) getCountry();
	}, [countries]);

	const handleChange = (e: SelectChangeEvent<string>) => {
		setShopCountryCode(e.target.value);
	};

	const findCountryName = (code: string) => {
		const country = countries.filter((con: Country) => con.country_code === code);
		return country[0].country_name;
	};

	return (
		<Container sx={{ my: 3, maxWidth: 500 }}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
				<Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
					{state.isModify ? 'SHOP ' : 'SHOP '}
				</Typography>
				<IconButton onClick={clickCancleBtn}>
					<CloseIcon />
				</IconButton>
			</Stack>
			<Stack direction="column" alignItems="flex-start">
				<Box sx={{ my: 2, width: '100%' }}>
					<Typography> </Typography>
					<Select fullWidth onChange={handleChange} value={shopCountryCode}>
						{countries.length !== 0 &&
							shopCountryCode === '' &&
							countries.map((country) => (
								<MenuItem
									key={country.country_code}
									value={country.country_code}
									data-country-name={country.country_name}
								>
									{country.country_name}
								</MenuItem>
							))}
						{countries.length !== 0 && shopCountryCode !== '' && (
							<MenuItem key={shopCountryCode} value={shopCountryCode} selected={true}>
								{findCountryName(shopCountryCode)}
							</MenuItem>
						)}
					</Select>
				</Box>
				<TextField
					fullWidth
					type="text"
					id="outlined-basic"
					label=""
					variant="outlined"
					name="shop_name"
					onChange={changeShopName}
					value={shopName}
				/>
				<Box sx={{ my: 4 }}>
					<Typography variant="h6"></Typography>
					<IconButton
						sx={{
							background: '#7BC6F6',
							color: '#fff',
							borderRadius: 2,
							mr: 2,
							':hover': {
								background: '#1976d2',
							},
						}}
						onClick={handleOpen}
					>
						{shopType === 0 ? <AddIcon /> : <ShopCategoryIcon category={shopType} />}
					</IconButton>
				</Box>
			</Stack>
			{state.isModify ? (
				<Button
					component="label"
					variant="contained"
					fullWidth
					sx={{
						background: '#7BC6F6',
						boxShadow: 'none',
						my: 3,
						borderRadius: 2,
						':hover': {
							boxShadow: 'none',
						},
					}}
					onClick={modifyShop}
				>
					
				</Button>
			) : (
				<Button
					component="label"
					variant="contained"
					fullWidth
					sx={{
						background: '#7BC6F6',
						boxShadow: 'none',
						my: 3,
						borderRadius: 2,
						':hover': {
							boxShadow: 'none',
						},
					}}
					onClick={submitShop}
				>
					
				</Button>
			)}

			<Modal
				open={open}
				onClose={handleClose}
				aria-labelledby="modal-modal-title"
				aria-describedby="modal-modal-description"
			>
				<Box sx={modalStyle}>
					<Typography id="modal-modal-title" variant="h6" fontWeight="bold" sx={{ mb: 2 }}>
						 
					</Typography>
					<Grid container spacing={3}>
						{shops.map((shop, index) => (
							<Grid item xs={4} key={index}>
								<Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
									<IconButton
										sx={{
											background: '#95D6FF',
											color: '#fff',
											borderRadius: 2,
											':hover': {
												background: '#1976d2',
											},
										}}
										data-shop-type={shop.shopType}
										onClick={selectShopType}
									>
										<ShopCategoryIcon category={shop.shopType} />
									</IconButton>
									<Typography sx={{ fontWeight: 'bold', mt: 1 }}>{shop.shopName}</Typography>
								</Box>
							</Grid>
						))}
					</Grid>
				</Box>
			</Modal>
		</Container>
	);
}

export default ShopAdd;
