import React from 'react';

import DirectionsBusIcon from '@mui/icons-material/DirectionsBus';
import FlightIcon from '@mui/icons-material/Flight';
import HotelIcon from '@mui/icons-material/Hotel';
import LocalPlayIcon from '@mui/icons-material/LocalPlay';
import PendingIcon from '@mui/icons-material/Pending';
import RestaurantIcon from '@mui/icons-material/Restaurant';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';

interface ShopCategoryIconProps {
	category: number;
}

function ShopCategoryIcon({ category }: ShopCategoryIconProps): any {
	let icon = null;

	if (category === 1) icon = <HotelIcon />; // 
	else if (category === 2) icon = <FlightIcon />; // 
	else if (category === 3) icon = <DirectionsBusIcon />; // 
	else if (category === 4) icon = <LocalPlayIcon />; // 
	else if (category === 5) icon = <RestaurantIcon />; // 
	else if (category === 6) icon = <ShoppingBasketIcon />; // 
	else icon = <PendingIcon />; // 

	return icon;
}

export default ShopCategoryIcon;
