import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

import CloseIcon from '@mui/icons-material/Close';
import { IconButton } from '@mui/material';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';

import api from '../../api/api';
import AccountSelectList from '../../components/AccountItem/AccountSelectList';
import useNumberFormat from '../../hooks/useNumberFormat';

interface Account {
	account_number: string;
	balance: number;
	bank_code: string;
}
function Payment() {
	const navigate = useNavigate();
	const { state } = useLocation();
	const { formattedValue, handleValueChange } = useNumberFormat('');
	const [showAccounts, setShowAccounts] = useState(false);
	const [selectedAccount, setSelectedAccount] = useState<Account | null>(null);

	const [accountPassword, setAccountPassword] = useState(''); //  

	const handleAccountPasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		setAccountPassword(e.target.value);
	};

	//  
	const clickCancleBtn = () => {
		navigate('/shop');
	};

	const [accounts, setAccounts] = useState<Account[]>([]);
	const fetchAccountData = async () => {
		try {
			const response = await api.get('account');
			if (response.status === 200) setAccounts(response.data);
		} catch (error: any) {
			console.error(error);
		}
	};

	// 
	const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		const data = new FormData(e.currentTarget);
		const amountString = data.get('amount'); // '1,000'  
		if (state.shopId !== '' && amountString != null && typeof amountString === 'string') {
			const amountNumber = parseFloat(amountString.replace(/,/g, ''));
			const paymentReq = {
				sender_account_num: selectedAccount?.account_number,
				shop_id: state.shopId,
				payment_amount: amountNumber,
				password: data.get('accountPassword'),
			};

			try {
				const res = await api.post(`/payment`, paymentReq);
				if (res.status === 200) {
					alert(' ');
					navigate('/shop');
				}
			} catch (error: any) {
				if (error.response.status == 401) {
					window.location.replace('/main');
				} else {
					window.alert(' ');
				}
			}
		}
	};

	useEffect(() => {
		fetchAccountData();
	}, []);

	const handleAccountSelect = (account: Account) => {
		setSelectedAccount(account);
		setShowAccounts(false); //     (   )
	};

	const isButtonEnabled =
		parseFloat(formattedValue.replace(/,/g, '')) > 0 &&
		accountPassword.length == 4 &&
		selectedAccount &&
		!showAccounts;

	return (
		<Container component="main">
			<CssBaseline />

			<Box
				sx={{
					my: 3,
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'center',
				}}
			>
				<Stack
					display="flex"
					direction="row"
					alignItems="center"
					justifyContent="space-between"
					width="100%"
					mb={5}
				>
					<Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
						
					</Typography>
					<IconButton onClick={clickCancleBtn}>
						<CloseIcon />
					</IconButton>
				</Stack>
				<Box component="form" sx={{ mt: 3, width: '100%' }} onSubmit={handleSubmit}>
					<Grid container spacing={2}>
						<Grid item xs={12}>
							<Button onClick={() => setShowAccounts(!showAccounts)}>
								{showAccounts ? '  .' : '  '}
							</Button>
							{showAccounts && (
								<div>
									{accounts.map((account) => (
										<div
											key={account.account_number}
											onClick={() => handleAccountSelect(account)} //     
										>
											<AccountSelectList
												key={account.account_number}
												balance={account.balance}
												bankcode={account.bank_code}
												accountNum={account.account_number}
											/>
										</div>
									))}
								</div>
							)}
							{selectedAccount &&
								!showAccounts && ( //     
									<div>
										<AccountSelectList
											key={selectedAccount.account_number}
											balance={selectedAccount.balance}
											bankcode={selectedAccount.bank_code}
											accountNum={selectedAccount.account_number}
										/>
									</div>
								)}
						</Grid>
						<Grid item xs={12}>
							<TextField
								required
								fullWidth
								id="amount"
								label=" "
								name="amount"
								value={formattedValue}
								onChange={handleValueChange}
							/>
						</Grid>
						<Grid item xs={12} sx={{ mt: 2 }}>
							<Typography sx={{ marginBottom: '0.5rem', marginTop: '1rem' }}>
								 4 .
							</Typography>
							<TextField
								required
								fullWidth
								id="accountPassword"
								label="  "
								name="accountPassword"
								variant="standard"
								type="password"
								value={accountPassword}
								onChange={handleAccountPasswordChange}
							/>
						</Grid>
					</Grid>
					<Button
						fullWidth
						type="submit"
						variant="contained"
						disabled={!isButtonEnabled}
						sx={{
							mt: 3,
							mb: 2,
							background: '#7BC6F6',
							boxShadow: 'none',
							':hover': {
								boxShadow: 'none',
							},
						}}
					>
						
					</Button>
				</Box>
			</Box>
		</Container>
	);
}

export default Payment;
