import React, { useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import Modal from '@mui/material/Modal';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';

import api from '../../api/api';
import bankName from '../../Data/BankName';
import BankIcon from '../BankIcon/BankIcon';

interface accountListItemProps {
	balance: number;
	bankcode: string;
	accountNum: string;
}

function AccountListItem({ balance, bankcode, accountNum }: accountListItemProps) {
	const navigate = useNavigate();

	const accountNumRef = useRef<HTMLDivElement | null>(null);
	const [modalOpen, setModalOpen] = useState(false);
	const [password, setPassword] = useState('');
	const handleAccountClick = () => {
		const accountNumElement = accountNumRef.current;

		if (accountNumElement) {
			const accountNumValue = accountNumElement.innerText;

			navigate(`/accountdetail`, {
				state: { accountNum: accountNumValue, balance: balance, bankcode: bankcode },
			});
		}
	};
	const handleCopyClipBoard = () => {
		const accountNumElement = accountNumRef.current;
		if (accountNumElement) {
			const accountNumValue = accountNumElement.innerText;
			try {
				navigator.clipboard.writeText(accountNumValue);
				alert(' .');
			} catch (error) {
				alert('  .');
			}
		}
	};

	const handleChangePassword = (event: React.ChangeEvent<HTMLInputElement>) => {
		setPassword(event.target.value);
	};
	const handleDeleteAccount = () => {
		setModalOpen(true);
	};

	const handleCloseModal = () => {
		setModalOpen(false);
	};

	const handleDeleteConfirm = async () => {
		try {
			const accountDeleteReqDto = {
				bank_code: { bankcode }.bankcode,
				account_num: { accountNum }.accountNum,
				account_password: password,
			};
			const res = await api.patch('account', accountDeleteReqDto);
			if (res.status === 200) {
				window.location.replace('/main');
			}
		} catch (error: any) {
			if (error.response.status == 401) {
				window.location.replace('/main');
			} else {
				window.alert('  ');
			}
		}

		handleCloseModal();
	};
	const clickTransfer = () => {
		navigate(`/transfer`, {
			state: {
				senderAccountNum: accountNum,
			},
		});
	};

	const formattedBalance = balance.toLocaleString();

	return (
		<Grid item xs={12}>
			<Card
				sx={{
					height: '100%',
					display: 'flex',
					flexDirection: 'column',
					position: 'relative',
					':hover': {
						cursor: 'pointer',
						background: '#efefef',
					},
				}}
			>
				<CardContent sx={{ flexGrow: 1 }} onClick={handleAccountClick}>
					<Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'start' }}>
						<BankIcon code={bankcode} />
						<Typography
							gutterBottom
							variant="h6"
							component="h2"
							id="accountNum"
							ref={accountNumRef}
							sx={{ ml: 1, mb: 0, fontFamily: 'OTWelcomeRA' }}
						>
							{bankName[bankcode]}
						</Typography>
						<Typography
							gutterBottom
							variant="h6"
							component="h2"
							id="accountNum"
							ref={accountNumRef}
							sx={{ ml: 1, mb: 0, fontWeight: 'bold', fontFamily: 'OTWelcomeRA' }}
						>
							{accountNum}
						</Typography>
					</Box>
					<Typography>{formattedBalance}</Typography>
				</CardContent>
				<CardActions
					sx={{
						alignSelf: 'flex-end',
						marginTop: 'auto',
						position: 'absolute',
						top: '50%',
						fontFamily: 'OTWelcomeRA',
					}}
				>
					<Button size="small" onClick={clickTransfer}>
						
					</Button>
					<Button size="small" onClick={handleDeleteAccount}>
						
					</Button>
					<Button size="small" onClick={handleCopyClipBoard}>
						  
					</Button>
				</CardActions>
			</Card>
			<Modal
				open={modalOpen}
				onClose={handleCloseModal}
				aria-labelledby="modal-title"
				aria-describedby="modal-description"
			>
				<Box
					sx={{
						position: 'absolute',
						top: '50%',
						left: '50%',
						transform: 'translate(-50%, -50%)',
						bgcolor: 'background.paper',
						boxShadow: 24,
						p: 4,
						maxWidth: 400,
						width: '90%',
						textAlign: 'center',
					}}
				>
					<h2 id="modal-title"> </h2>
					<p id="modal-description">  ?</p>
					<TextField
						label=""
						type="password"
						variant="outlined"
						fullWidth
						value={password}
						onChange={handleChangePassword}
						sx={{ mb: 2 }}
						inputProps={{ maxLength: 4 }}
					/>
					<Button variant="contained" onClick={handleDeleteConfirm} disabled={password.length != 4}>
						
					</Button>
					<Button variant="contained" onClick={handleCloseModal} sx={{ ml: 2 }}>
						
					</Button>
				</Box>
			</Modal>
		</Grid>
	);
}

export default AccountListItem;
